/**
 * 
 * @param start {x:number, y:number}
 * @param end {x:number, y:number}
 * @returns {x:number, y:number}
 */
function getMiddlePoint(start, end) {
  var x = (end.x - start.x) / 2 + start.x;
  var y = (end.y - start.y) / 2 + start.y;
  return {
    x: x,
    y: y
  };
}
